@import url(https://cdnjs.cloudflare.com/ajax/libs/meyer-reset/2.0/reset.css);
@import url(https://fonts.googleapis.com/css2?family=Cabin:wght@700&family=Lobster&family=Roboto:wght@300&display=swap);
.body {
  width: 85%;
}
.ordinary_header {
  background-color: white;
  width: 100%;
  height: 35vh;
  font-size: 500%;
  font-family: "Lobster", cursive;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  overflow: hidden;
}
.ordinary_header h5 {
  margin-top: 20px;
  font-size: 0.4em;
  background: linear-gradient(to left, #ec6f66, #f3a183);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: "Cabin", sans-serif;
}
.loader {
  font-family: "Lobster", cursive;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 100px;
}
.main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.ordinary_container {
  background: linear-gradient(
    to left,
    #ed4264,
    #ffedbc
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  /* background-color: #ced6e0; */
  padding: 30px 10px;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-auto-flow: row;
  justify-items: center;

  grid-row-gap: 40px;

  row-gap: 40px;
}
.ordinary__list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 75%;
  height: 30vh;
  background-color: #f5f6fa;
  border-radius: 15px;
  position: relative;
  padding-top: 30px;
  box-shadow: 0px 20px 30px -10px;
  transition: all 0.3s;
}
.ordinary__list:hover {
  transform: scale(1.1);
}
.ordinary__list span {
  font-family: "Cabin", sans-serif;
  text-align: center;
}
.ordinary_link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: inherit;
}
.ordinary__list li {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70%;
  height: 30%;
  position: absolute;
  top: -25px;
  left: -25px;
  background-color: #273c75;
  padding: 15px;

  font-size: 0.9em;
  color: white;
  border-radius: 40px 40px 10px 40px;
}
.ordinary__list img {
  width: 80%;

  border-radius: 150px;
}

