.sp_body {
  width: 100%;
  height: 100vh;
}

.sp_header {
  width: 100%;
  height: 23%;
  background-color: white;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.sp_header .header_title {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to left, #141e30, #243b55);
  text-align: center;
}
.sp_header .header_title .title_font {
  font-family: "Lobster", cursive;
  font-size: 500%;
  background: linear-gradient(
    to left,
    #8e9eab,
    #eef2f3
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.sp_header .header_sub {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 250%;
  font-family: "Cabin", sans-serif;
  background: linear-gradient(to left, #8e9eab, #eef2f3);
}

.sp_main {
  width: 100%;
  background-color: white;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.sp_main_1 {
  position: relative;
  width: 100%;
  height: 100%;
  font-size: 5em;
}
.sp_main_2 {
  position: relative;
  width: 100%;
  height: 100%;
  font-size: 5em;
}

.sp_imgLeft_title,
.sp_imgRight_title {
  font-family: "Cabin", sans-serif;
  opacity: 0.6;
  left: 0%;
  right: 0%;
  top: 40%;
  color: white;
  position: absolute;
  text-align: center;
}

.sp_main_1 img,
.sp_main_2 img {
  width: inherit;
  height: inherit;
}
.sp_footer {
  font-family: "Cabin", sans-serif;
  width: 100%;
  height: 10%;
  background-color: inherit;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.sp_footer div {
  color: white;
  font-size: 100%;
  /* margin-right: 140px; */
  display: flex;
  align-items: center;
}
.sp_footer div span {
  margin: 0px 5px;
}
.sp_footer div i {
  font-size: 30px;
}
.link {
  width: 100%;
  height: 100%;
  text-decoration: none;
  border: none;
}
.link:hover .sp_imgLeft_title,
.link:hover .sp_imgRight_title {
  opacity: 1;
}
.link:hover .sp_main_1,
.link:hover .sp_main_2 {
  opacity: 0.8;
}
.link:hover .sp_imgLeft_title,
.link:hover .sp_imgRight_title,
.link:hover .sp_main_1,
.link:hover .sp_main_2 {
  transition: all 0.3s ease-in-out;
}

.ordinary_detail {
  display: flex;
  justify-content: center;
}
.detail_loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Lobster", cursive;
  color: white;
  font-size: 100px;
}
.ordinary_detail_main {
  width: 60vw;
  display: block;
  /*  background: linear-gradient(to top, #e0eafc, #cfdef3); */
  background: linear-gradient(to top, #8e9eab, #eef2f3);
}
.ordinary_detail_main .detail_top {
  width: 100%;
  height: 25vh;
}
.ordinary_detail_main .detail_title {
  width: 100%;
  height: 100%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.ordinary_detail_main .detail_title span:nth-child(1) {
  font-family: "Lobster", cursive;
  /* font-size: 4rem; */
  font-size: 400%;
}
.ordinary_detail_main .detail_title span:nth-child(2) {
  font-family: "Cabin", sans-serif;
  font-size: 1.3em;
  margin-top: 20px;
}
.detail_main1 {
  width: 100%;
  /* height: 50%; */
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.detail_main1 .image_desc {
  font-family: "Cabin", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 15px;
  height: 100%;
}
.detail_main1 .image_desc .mix_desc {
  width: 14vw;
  height: 7vh;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 30px;
  font-size: 20px;
}
.main_image {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.detail_main1 .image_desc p {
  width: 25vw;
  height: 40vh;
  background-color: inherit;
  font-size: 1.3em;
  font-weight: 100;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  overflow: scroll;
  text-align: center;
  line-height: 30px;
}

.detail_main1 img {
  width: 100%;
}

.footer {
  width: 100%;

  background-color: beige;
  display: grid;
  justify-content: center;
  align-items: center;
}
.footer_title {
  width: 100%;
  margin-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0px;
}
.footer_title span {
  font-size: 20px;
  width: 200px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-radius: 30px;
  font-family: "Cabin", sans-serif;
}
.footer .images {
  padding: 20px 10px;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  /* grid-auto-flow: column; */
  grid-auto-flow: row;
  justify-items: center;
  align-items: center;
  grid-column-gap: 3em;
  -webkit-column-gap: 3em;
          column-gap: 3em;
  grid-row-gap: 1em;
  row-gap: 1em;
}
.footer .images div {
  padding: 20px;
  width: 100%;
  height: 100%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 30px;
  font-family: "Roboto", sans-serif;
  text-align: center;
}
.footer .images div hr {
  width: 80%;
}
.footer .images img {
  width: 90%;
  margin-bottom: 5px;
}

#root {
  width: 100%;
}
* {
  box-sizing: border-box;
}
html,
body {
  background-color: #4d5766;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

